export default {
    layout: "Adm-content",
    mounted() {
        this.getbatch();

    },
    data() {
        return {
            batchs: [],
            errors: {},
        };
    },
    methods: {
        getbatch() {
            this.$axios
                .$get("admission/batch-show")
                .then((res) => {
                    console.log(res);
                    this.batchs = res;
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        batchDelete(id) {
            if (confirm("Are you sure to delete this batch?")) {
                this.$axios
                    .$get("admission/batch-delete/" + id)
                    .then((res) => {
                        this.getbatch();
                        this.$toaster.error(res.message);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }
        },
        batchStatus(id, status) {
            this.$axios
                .$get("admission/batch-status/" + id)
                .then((res) => {
                    this.getbatch();
                    this.$toaster.success(res.message);
                })
                .catch((err) => {
                    console.log(err);
                });
        },
    },
};
